import React from 'react';

// 各種コンポーネント取得
import Router from './../config/routes'
import Parts from './../config/parts';

// 各種設定値取得
import AllData from './../config/allData';
import Functions from './../config/functions';
import States from './../config/states';
import Pages from './../config/pages';

class Layout extends React.Component {

	constructor(props) {
		super(props);

		/***** state初期値設定 *****/
		this.state = {
			// ページ表示設定
			page: States.page.Top,
			 // サイドエリア表示設定
			menu: States.menu.hide,
			// サイドエリアの子リスト表示
			list: States.list.hide,
		};

		/***** sessionStorrage初期値設定(戻るボタン用) *****/
		// 現在のページのstateをセット
		for (var i = 1; i <= 5; i++) {
			sessionStorage.setItem('back' + i + 'Page', this.state.page);
		}
		// 戻るボタンを押した回数をセット
		sessionStorage.setItem('backCount', "1");

		/***** functionの初期値設定 *****/
		this.funcs = [
			{
				"Top": Functions.Page.bind(this, States.page.Top, Pages.Top.Path),
				"Menu": Functions.Menu.bind(this),
				"Back": Functions.Back.bind(this),
			},
			{
				"Page1": {
					"L1": Functions.List.bind(this, States.list.list1),
					"S1": Functions.PageSection.bind(this, States.page.Page1.S1, Pages.Page1.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page1.S2, Pages.Page1.Section2.Path),
					"S3": Functions.PageSection.bind(this, States.page.Page1.S3, Pages.Page1.Section3.Path),
				},
			},
			{
				"Page2": {
					"L2": Functions.List.bind(this, States.list.list2),
					"S1": Functions.PageSection.bind(this, States.page.Page2.S1, Pages.Page2.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page2.S2, Pages.Page2.Section2.Path),
				},
			},
			{
				"Page3": {
					"L3": Functions.List.bind(this, States.list.list3),
					"S1": Functions.PageSection.bind(this, States.page.Page3.S1, Pages.Page3.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page3.S2, Pages.Page3.Section2.Path),
					"S3": Functions.PageSection.bind(this, States.page.Page3.S3, Pages.Page3.Section3.Path),
					"S4": Functions.PageSection.bind(this, States.page.Page3.S4, Pages.Page3.Section4.Path),
				},
			},
			{
				"Page4": {
					"L4": Functions.List.bind(this, States.list.list4),
					"S1": Functions.PageSection.bind(this, States.page.Page4.S1, Pages.Page4.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page4.S2, Pages.Page4.Section2.Path),
				},
			},
			{
				"Page5": {
					"L5": Functions.List.bind(this, States.list.list5),
					"S1": Functions.PageSection.bind(this, States.page.Page5.S1, Pages.Page5.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page5.S2, Pages.Page5.Section2.Path),
					"S3": Functions.PageSection.bind(this, States.page.Page5.S3, Pages.Page5.Section3.Path),
					"S4": Functions.PageSection.bind(this, States.page.Page5.S4, Pages.Page5.Section4.Path),
					"S5": Functions.PageSection.bind(this, States.page.Page5.S5, Pages.Page5.Section5.Path),
					"S6": Functions.PageSection.bind(this, States.page.Page5.S6, Pages.Page5.Section6.Path),
					"S7": Functions.PageSection.bind(this, States.page.Page5.S7, Pages.Page5.Section7.Path),
					"S8": Functions.PageSection.bind(this, States.page.Page5.S8, Pages.Page5.Section8.Path),
					"S9": Functions.PageSection.bind(this, States.page.Page5.S9, Pages.Page5.Section9.Path),
					"S10": Functions.PageSection.bind(this, States.page.Page5.S10, Pages.Page5.Section10.Path),
					"S11": Functions.PageSection.bind(this, States.page.Page5.S11, Pages.Page5.Section11.Path),
					"S12": Functions.PageSection.bind(this, States.page.Page5.S12, Pages.Page5.Section12.Path),
					"S13": Functions.PageSection.bind(this, States.page.Page5.S13, Pages.Page5.Section13.Path),
					"S14": Functions.PageSection.bind(this, States.page.Page5.S14, Pages.Page5.Section14.Path),
					"S15": Functions.PageSection.bind(this, States.page.Page5.S15, Pages.Page5.Section15.Path),
					"S16": Functions.PageSection.bind(this, States.page.Page5.S16, Pages.Page5.Section16.Path),
					"S17": Functions.PageSection.bind(this, States.page.Page5.S17, Pages.Page5.Section17.Path),
					"S18": Functions.PageSection.bind(this, States.page.Page5.S18, Pages.Page5.Section18.Path),
					"S19": Functions.PageSection.bind(this, States.page.Page5.S19, Pages.Page5.Section19.Path),
					"S20": Functions.PageSection.bind(this, States.page.Page5.S20, Pages.Page5.Section20.Path),
				},
			},
			{
				"Page6": {
					"L6": Functions.List.bind(this, States.list.list6),
					"S1": Functions.PageSection.bind(this, States.page.Page6.S1, Pages.Page6.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page6.S2, Pages.Page6.Section2.Path),
					"S3": Functions.PageSection.bind(this, States.page.Page6.S3, Pages.Page6.Section3.Path),
					"S4": Functions.PageSection.bind(this, States.page.Page6.S4, Pages.Page6.Section4.Path),
				},
			},
			{
				"Page7": {
					"L7": Functions.List.bind(this, States.list.list7),
					"S1": Functions.PageSection.bind(this, States.page.Page7.S1, Pages.Page7.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page7.S2, Pages.Page7.Section2.Path),
				},
			},
			{
				"Page8": {
					"L8": Functions.List.bind(this, States.list.list8),
					"S1": Functions.PageSection.bind(this, States.page.Page8.S1, Pages.Page8.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page8.S2, Pages.Page8.Section1.Path),
				},
			},
			{
				"Page9": {
					"L9": Functions.List.bind(this, States.list.list9),
					"S1": Functions.PageSection.bind(this, States.page.Page9.S1, Pages.Page9.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page9.S2, Pages.Page9.Section1.Path),
				},
			},
			{
				"Page10": {
					"L10": Functions.List.bind(this, States.list.list10),
					"S1": Functions.PageSection.bind(this, States.page.Page10.S1, Pages.Page10.Section1.Path),
					"S2": Functions.PageSection.bind(this, States.page.Page10.S2, Pages.Page10.Section1.Path),
				},
			},

		];

		/***** ページデータ設定 *****/
		this.allData = AllData(this.funcs);

	}

	// メニューエリア以外押下時のメソッド
	handleClickOutside() {
		this.setState({menu: "hide"});
	}

	render() {
		return (
			<div className="flex-box">

				{/* サイドエリア */}
				{this.state.menu === States.menu.show && (
					<Parts.Aside
						allData={this.allData}
						states ={this.state}
						handleClickOutside={this.handleClickOutside.bind(this)}
					/>
				)}

				{/* コンテンツエリア */}
				<div className="contents">

					{/* ヘッダーエリア */}
					<Parts.Header
						allData={this.allData}
						states={this.state}
						func={this.funcs}
					/>

					{/* メインエリア */}
					<main className="main">
						{/* コンテンツ(ページ)切替 */}
						<Router.Page
							allData={this.allData}
							states={this.state}
						/>
					</main>

				</div>
			</div>
		);
	}

}

export default Layout;
